import React, { useEffect, useState } from "react";
import Header from './Header/Header'
import Footer from './Footer/Footer'
import Main from './Main/Main'
import './styles/App.scss'
import { Typography } from "@mui/material";




function App() {
  
  const [isLoading, setIsLoading] = useState(true);
  useEffect(()=>{
    setIsLoading(true)
    setTimeout(()=>{
      setIsLoading(false);
    }, 3000)
  }, [])

  const openingText = 'Kanazawa Univ'
  return (
    <>
    {isLoading ?
      <Typography className="opening">
        <Typography 
          fontSize={{xs: '48px',sm: '64px', md: '103px'}}
          className="opening-text">
          {Array.from(openingText).map((word, index)=>{
            return(
              <span>{word === ' ' ? '\u00A0' : word}</span>
            )
          })}
        </Typography>
      </Typography>        
      :
      <Typography className="App">
        
        <Header />
        <Main />
        <Footer />
      </Typography>
  }
  </>
  );
}


export default App;
