import React, { useEffect, useState } from 'react';
import { Navbar, Container, Nav } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope, faMagnifyingGlass, faFilePen, faCalendarDay,faArrowRight, } from '@fortawesome/free-solid-svg-icons'
import { Link as Scroll, animateScroll } from 'react-scroll';
import { Typography } from '@mui/material';
import Line from './components/Line';

const Header = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [windowWidth]);

  const NavItemsArray = [
    {to: "About", icon: faMagnifyingGlass},
    {to: "Activity", icon: faCalendarDay},
    {to: "Report", icon: faFilePen},
    {to: "Contact", icon: faEnvelope},
  ];
  
  return (
    <header>
      <Line />
      <Navbar expand="lg" bg='white'>
        <Container fluid>
          <Navbar.Brand href="#" className='jp-font-600'>金沢大学起業部</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              {NavItemsArray.map((NavItem) =>{
                return(
                  <Nav.Link className='nav-item'>
                    <Scroll to={NavItem.to}>
                    <Typography sx={{ display: 'flex', alignItems: 'center' }}>
                      {NavItem.to}
                      <FontAwesomeIcon icon={NavItem.icon} />
                      {windowWidth < 982 ? <FontAwesomeIcon icon={faArrowRight} style={{ marginLeft: 'auto' }} /> : ''}
                      </Typography>
                    </Scroll>
                  </Nav.Link>
                )
              })}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};

export default Header;